<template>
  <div class="van-hairline--top m-article-item">
    <van-card :thumb="article.cover">
      <template #title>
        <h3 class="m-article-item--title van-ellipsis">
          {{ article.title }}
        </h3>
      </template>
      <template #desc>
        <p class="m-article-item--desc van-multi-ellipsis--l2">
          {{ desc }}
        </p>
      </template>
      <template #bottom>
        <p class="m-article-item--author van-ellipsis">
          {{ author }}
        </p>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: 'VueBlogMArticleItem',
  props: {
    article: {
      type: Object
    }
  },
  data() {
    return {}
  },
  computed: {
    desc() {
      return this.article?.content.replace(/<[^>]*>|/g, '').slice(0, 80)
    },
    author() {
      return this.article?.author?.nickname
    }
  },
  mounted() {},

  methods: {}
}
</script>

<style lang="stylus">
.m-article-item
  margin-top 4px

.m-article-item p
  padding 4px 0

.m-article-item--title
  font-size 16px
  font-weight bold
</style>