<template>
  <div class="cropper-wrapper">
    <!-- <template v-if="!isPreview">
      <el-upload
        class="upload-demo"
        action=""
        drag
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">点击上传</div>
        <div class="el-upload__tip">
          支持绝大多数图片格式，单张图片最大支持5MB
        </div>
      </el-upload>
    </template> -->
    <el-image
      v-if="avatarURL"
      :src="currentImg"
      class="blog-user--avatar"
      fit="cover"
    />
    <div class="pre-box">
      <el-upload
        action=""
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
        <el-button type="primary" plain style="margin-left: 30px"
          >上传头像</el-button
        >
      </el-upload>
    </div>
    <el-button
      type="primary"
      plain
      style="margin-left: 10px"
      @click="handleModifyOrigin"
      >修改头像</el-button
    >
    <!-- vueCropper 剪裁图片实现-->
    <el-dialog
      title="更换个人头像"
      :visible.sync="dialogVisible"
      class="crop-dialog"
      append-to-body
    >
      <div class="cropper-content">
        <div class="cropper" style="text-align: center">
          <VueCropper
            ref="cropper"
            :mode="option.mode"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="option.info"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
            @cropMoving="cropMoving"
          />
        </div>
      </div>
      <!-- <el-upload
        class="upload-demo"
        action=""
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
      </el-upload> -->
      <p>提示: 使用鼠标滚轮可以放大缩小</p>
      <div class="action-box">
        <el-button
          type="primary"
          plain
          icon="el-icon-refresh-left"
          @click="rotateLeftHandle"
        />
        <el-button
          type="primary"
          plain
          icon="el-icon-refresh-right"
          @click="rotateRightHandle"
        />
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="finish" :loading="loading"
          >确认</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
//vuecropper 图片上传裁剪
import { VueCropper } from 'vue-cropper'
import instance from '@/api/httpBase'

export default {
  name: 'VueBlogUserAvatarUpload',
  components: {
    VueCropper
  },
  props: {
    avatarURL: {
      type: String
    }
  },
  data() {
    return {
      isPreview: false,
      dialogVisible: false,
      previewImg: '', // 预览图片地址
      // 裁剪组件的基础配置option
      option: {
        img: '', // 裁剪图片的地址
        mode: 'cover', //图片默认渲染方式
        info: false, // 裁剪框的大小信息
        outputSize: 1, // 裁剪生成图片的质量
        outputType: 'png', // 裁剪生成图片的格式
        canScale: true, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        canMoveBox: true, // 截图框能否拖动
        autoCropWidth: '300', // 默认生成截图框宽度
        autoCropHeight: '300', // 默认生成截图框高度
        fixedBox: false, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1, 1], // 截图框的宽高比例
        full: false, // 是否输出原图比例的截图
        original: false, // 上传图片按照原始比例渲染
        centerBox: true, // 截图框是否被限制在图片里面
        infoTrue: false // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      // 防止重复提交
      loading: false
    }
  },
  computed: {
    //原有用户头像||剪裁后的头像
    currentImg() {
      return this.previewImg || this.avatarURL
    }
  },
  methods: {
    //开始上传行为 唤起对话框
    uploadStart(url) {
      this.loading = false
      this.dialogVisible = true
      this.option.img = url
    },

    //获取原头像blob
    async getUrlBlob(url) {
      url = process.env.VUE_APP_PROXY + url.match(/(\/user.*)/)?.[0]
      let resBlob = await instance.get(url, {
        responseType: 'blob' // 要求返回服务器返回blob
      })
      let blobURL = URL.createObjectURL(resBlob)
      return blobURL

      // BASE64
      // let img = new Image()
      // img.src = url
      // img.onload = () => {
      //   let canvas = document.createElement('canvas') //创建canvas DOM元素
      //   canvas.width = img.width
      //   canvas.height = img.height
      //   let ctx = canvas.getContext('2d')
      //   ctx.drawImage(img, 0, 0, img.width, img.height)
      //   let ext = url.match(/\.([a-z\d]+)$/)[1] || 'jpg'
      //   let dataURL = canvas.toDataURL('image/' + ext)
      //   canvas = null
      //   return dataURL //获取Base64编码
      // }
    },

    // 修改原头像按钮
    async handleModifyOrigin() {
      let dataURL = await this.getUrlBlob(this.avatarURL)
      this.uploadStart(dataURL)
    },

    //上传前校验 限制图片大小和类型
    beforeAvatarUpload(file) {
      let errMsg
      let isImg = /^image\/(jpeg|png|gif|bmp)$/.test(file.type)
      let isSize = file.size < 5 * 1024 * 1024

      if (!isImg) {
        errMsg = '上传头像图片只能是 JPG/PNG/GIF/BMP 格式!'
      }
      if (!isSize) {
        errMsg = '上传头像图片大小不能超过5M !'
      }

      if (!isImg || !isSize) {
        this.$notify.error({
          title: '上传错误',
          message: errMsg
        })
      }

      return isImg && isSize
    },

    // 上传按钮
    handleChangeUpload(file) {
      let flag = this.beforeAvatarUpload(file.raw)
      if (!flag) return false

      // 将图片地址赋值给裁剪框显示图片
      this.$nextTick(async () => {
        // base64方式
        // this.option.img = await fileByBase64(file.raw)
        this.uploadStart(URL.createObjectURL(file.raw))
      })
    },

    // 左旋转
    rotateLeftHandle() {
      this.$refs.cropper.rotateLeft()
    },

    // 右旋转
    rotateRightHandle() {
      this.$refs.cropper.rotateRight()
    },

    // 下载
    // downloadHandle(type) {
    //   let aLink = document.createElement('a')
    //   aLink.download = 'author-img'
    //   if (type === 'blob') {
    //     this.$refs.cropper.getCropBlob((data) => {
    //       aLink.href = URL.createObjectURL(data)
    //       aLink.click()
    //     })
    //   } else {
    //     this.$refs.cropper.getCropData((data) => {
    //       aLink.href = data
    //       aLink.click()
    //     })
    //   }
    // },

    // 清理图片
    clearImgHandle() {
      this.option.img = ''
    },

    // 截图框移动回调函数
    cropMoving(data) {
      // 截图框的左上角 x，y和右下角坐标x，y
      // let cropAxis = [data.axis.x1, data.axis.y1, data.axis.x2, data.axis.y2]
      // console.log(cropAxis)
    },

    //图片截取完成(触发主动服务器上传)
    finish() {
      // 获取截图的 blob 数据
      let formData = new FormData()
      this.$refs.cropper.getCropBlob((blob) => {
        //blob数据放入formData 发送请求
        formData.append('user', blob)
        this.httpHandle(formData)

        this.loading = true
        this.dialogVisible = false
        this.previewImg = URL.createObjectURL(blob)
        this.isPreview = true
      })
      // 获取截图的 base64 数据
      // this.$refs.cropper.getCropData(data => {
      //     console.log(data)
      // })
    },

    async httpHandle(formData) {
      try {
        let { fileURL } = await this.$http({
          type: 'uploadAvatar',
          data: formData
        })
        //上传成功
        this.$bus.$emit('changeAvatar', fileURL)
      } catch (err) {
        this.$notify.error({
          title: '上传失败',
          message: '服务器内部错误'
        })
      }
    }
  }
}
</script>

<style lang="stylus">
.blog-user--avatar
  width 150px
  height 150px
  border-radius 50%

.cropper-wrapper
  display flex
  align-items center

.crop-dialog
  .cropper-content
    padding 0 40px
    &+p
      padding 10px 0
      text-align center
  .cropper
    width auto
    height 350px
  .action-box
    padding 10px 0
    display flex
    justify-content center
    button
      font-size 18px
      margin-left 20px
  .dialog-footer
    button
      width 100px
</style>