<template>
  <el-form :model="form" ref="elForm" status-icon label-width="100px">
    <!-- 针对item传入rule -->
    <el-form-item
      v-for="item in formData"
      :key="item.query"
      :label="item.label"
      :prop="item.query"
      :rules="validates?.[item.query]?.(formType) || rules?.[item.query]"
    >
      <!-- v-model是对应form 对象而不是字符串 -->
      <UserAvatarUpload
        v-if="item.type === 'avatar'"
        :avatarURL="form[item.query]"
      />
      <el-input
        v-if="/^(text|password)$/.test(item.type)"
        v-model="form[item.query]"
        :type="item.type"
        :placeholder="item.placeholder"
        :name="item.query"
        :show-password="item.query === ('password' || 'password_confirm')"
        autocomplete="off"
        clearable
        :disabled="item.readonly"
        :readonly="isUserInfo"
        :ref="item.query"
      >
        <el-button
          v-if="isUserInfo && !item.readonly"
          slot="append"
          icon="el-icon-edit"
          @click="modifyInput($refs?.[item.query][0].$refs.input)"
        ></el-button>
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import FORM_CONFIG from '@/configs/form.config'
import VALIDATE_CONFIG from '@/configs/validate.config'
import UserAvatarUpload from '@/components/user/UserAvatarUpload.vue'

export default {
  name: 'VueBlogBaseForm',
  components: { UserAvatarUpload },
  props: {
    formType: String
  },
  data() {
    let confirmPwd = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      form: {},
      rules: {
        password_confirm: [{ validator: confirmPwd }]
      }
    }
  },
  created() {
    if (this.isUserInfo) {
      //写入已有信息 对比formData 筛选仅保留form.config['userInfo']中的query项
      this.form = Object.fromEntries(
        Object.entries(this.$store.getters.userInfo).filter(([key]) =>
          this.formData.find((item) => item.query === key)
        )
      )
    }
  },
  //注册子组件修改头像事件
  mounted() {
    this.$bus.$on('changeAvatar', (newAvatar) => {
      this.form.avatar = newAvatar
    })
  },
  beforeDestroy() {
    this.$bus.$off('changeAvatar')
  },
  watch: {
    // 通过监听formType的变化重置表单
    formType() {
      this.resetForm()
    }
  },
  computed: {
    formData() {
      return FORM_CONFIG[this.formType]
    },
    validates() {
      return VALIDATE_CONFIG
    },
    isUserInfo() {
      return this.formType === 'userInfo'
    }
  },
  methods: {
    resetForm() {
      //重置form对象
      this.form = {}
      //重置校验状态
      this.$refs['elForm'].resetFields()
    },
    //用户中心-点击按钮修改
    modifyInput(targetInput) {
      targetInput.removeAttribute('readonly')
    }
  }
}
</script>

<style lang="stylus" >
input[readonly='readonly']
  color #ccc
</style>