<template>
  <van-form ref="vanForm" class="blogm-form">
    <van-field
      label-align="center"
      v-for="field in formData"
      :key="field.query"
      :name="field.label"
      :type="field.type"
      :label="field.label"
      :readonly="field.readonly"
      :placeholder="field.placeholder"
      :rules="formRules(field)"
      v-model="form[field.query]"
    />
  </van-form>
</template>

<script>
/*
      :rules="[{ required: true, message: '请填写密码' }]"
      其他可选
      pattern  正则表达式
      trigger(与element不同,是onChange、onBlur)
      validator
*/
import VALIDATES from '@/configs/validate.config'
import FORM_CONFIG from '@/configs/form.config'
import { capitalize } from 'lodash'

export default {
  name: 'VueBlogBaseForm',
  props: {
    formType: {
      type: String
    }
  },
  data() {
    return {
      form: {}
    }
  },
  created() {
    if (this.formType === 'userInfo') {
      this.formatUserInfo()
    }
  },
  //注册子组件修改头像事件
  mounted() {
    this.$bus.$on('change-avatar', (newAvatar) => {
      this.form.avatar = newAvatar
    })
  },
  beforeDestroy() {
    this.$bus.$off('change-avatar')
  },
  watch: {
    formType(newType) {
      this.form = {}
      //用户信息赋值
      if (newType === 'userInfo') {
        this.formatUserInfo()
      }
    }
  },
  computed: {
    formData() {
      return FORM_CONFIG?.[this.formType]?.filter((item) => {
        return item.query !== 'avatar'
      })
    },
    validates() {
      return VALIDATES
    }
  },
  methods: {
    formatUserInfo() {
      //写入已有信息 对比formData 筛选仅保留form.config['userInfo']中的query项
      this.form = Object.fromEntries(
        Object.entries(this.$store.getters.userInfo).filter(([key]) => {
          return FORM_CONFIG['userInfo'].find((item) => item.query === key)
        })
      )
    },
    formRules(field) {
      let rules
      //确认密码
      if (field.query === 'password_confirm') {
        rules = [
          {
            validator: this.confirmPwd,
            message: '两次输入的密码不一致',
            trigger: 'onBlur'
          }
        ]
        return rules
      }
      rules = this.formatValidate(
        this.validates?.[field.query]?.(this.formType)
      )
      return rules
    },
    confirmPwd(value) {
      return value === this.form.password
    },
    // lodash库 trigger:blur => onBlur
    formatValidate(rules) {
      return rules?.map((item) => {
        item.trigger && (item.trigger = 'on' + capitalize(item.trigger))
        return item
      })
    }
  }
}
</script>

<style lang="stylus">
.blogm-form.van-form
  padding 10px 20px
  .van-cell
    padding 10px 0
</style>