export default {
  // trigger 选择在input何时事件触发校验 pattern 填入正则
  username(formType) {
    const rules = (formType !== 'login') ? [
      { required: true, message: '用户名必填', trigger: 'blur' },
      { pattern: /^(?!\d+$)(?![A-Za-z]+$)[\d\w]{6,10}$/, message: '用户名必须是6-10位字母数字组合', trigger: 'blur' },
    ] : [{ required: true, message: '用户名必填', trigger: 'blur' }]
    return rules
  },
  password(formType) {
    const rules = (formType !== 'login') ? [
      { required: true, message: '密码必填', trigger: 'blur' },
      { pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[\w\d!@#$%^&*().]{8,18}$/, message: '密码必须是8-18位数字、大小写字母组合', trigger: 'blur' },
    ] : [{ required: true, message: '密码必填', trigger: 'blur' }]
    return rules
  },
  email() {
    return [
      { required: true, message: '邮箱必填', trigger: 'blur' },
      { type: 'email', message: '请输入合法的邮箱地址', trigger: 'blur' },
    ]
  },
  //这里指的是分区名称
  name() {
    return [
      { required: true, message: '分区名称必填', trigger: 'blur' },
    ]
  },
  nickname() {
    return [
      { required: true, message: '昵称必填', trigger: 'blur' },
      { pattern: /^[\da-zA-Z\u0391-\uFFE5]{1,8}$/, message: '昵称必须是1-8位数字/字母/汉字', trigger: 'blur' },
    ]
  },
  signature() {
    return [
      { required: true, message: '签名必填', trigger: 'blur' },
      { pattern: /^.{1,30}$/, message: '签名长度 1~30 位', trigger: 'blur' },
    ]
  },
}

