<template>
  <div class="blogm-article-list">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="文章已全部加载完~"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        @load="getArticles"
        offset="150"
      >
        <router-link
          :to="{ name: 'mArticle', params: { id: article._id } }"
          v-for="article in articleList"
          :key="article._id"
        >
          <mArticleItem :article="article" />
        </router-link>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import mArticleItem from '@/components/mobile/article/mArticleItem.vue'
import QS from 'qs'
//lodash 深拷贝方法
import { cloneDeep } from 'lodash'
export default {
  name: 'VueBlogMArticleList',
  components: { mArticleItem },
  props: {
    columnId: {
      type: String
    }
  },
  data() {
    return {
      articleList: [],
      //分区栏目文章浏览缓存对象
      columnArticleCache: {},
      loading: false,
      //加载完成
      finished: false,
      //下拉刷新
      refreshing: false,
      //请求错误时改为true
      error: false,
      //当前页
      page: 1,
      //每页数量
      size: 5,
      //总搜索结果数
      total: 0
    }
  },
  watch: {
    //监听columnId变化(分类标签)
    columnId(newId) {
      if (!newId.trim().length) return
      //如果已存在缓存,调用缓存内的数据
      if (this.columnArticleCache[newId]) {
        this.articleList = this.columnArticleCache[newId]
        return false
      }
      //否则去获取对应column的数据
      this.refreshing = true
      this.onRefresh()
    }
  },
  mounted() {
    //搜索事件监听
    this.$bus.$on('header-search', this.searchArticle)
  },
  beforeDestroy() {
    this.$bus.$off('header-search', this.searchArticle)
  },
  methods: {
    searchArticle(query) {
      this.search = query
      this.refreshing = true
      this.onRefresh()
    },
    getQuery() {
      //如果是来自分区的请求
      let column = this.columnId || undefined
      //搜索请求
      let search = this.search || undefined
      //用于去除空键值
      let query = JSON.parse(
        JSON.stringify({
          column,
          search
        })
      )
      return query
    },
    async getArticles() {
      try {
        let data = { size: this.size, page: this.page }
        //监视路由是否携带query
        let query = this.getQuery()
        if (Object.entries(query).length > 0) {
          data.query = QS.stringify(query)
        }

        let result = await this.$http({ type: 'getArticles', data })
        if (this.refreshing) {
          this.articleList = []
          this.refreshing = false
        }
        //如果已经完成了所有加载了 finished
        if (this.articleList.length >= result.total) {
          this.finished = true
          this.cacheColumnArticle(result.list)
          return
        }
        //缓存
        this.cacheColumnArticle(result.list)
        //添加到页面
        this.articleList.push(...result.list)
        //页数+1
        this.page++
        // 加载状态结束
        this.loading = false
      } catch (err) {
        //错误处理
        //console.log(err)
        this.error = true
      }
    },
    cacheColumnArticle() {
      if (this.columnId) {
        this.columnArticleCache[this.columnId] = cloneDeep(this.articleList)
      }
    },
    onRefresh() {
      this.page = 1
      // 清空列表数据
      this.finished = false
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true
      // 重新加载数据
      this.getArticles()
    }
  }
}
</script>

<style lang="stylus"></style>